*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;

  font-family: sans-serif;
  line-height: 1.3;
}

html {
  font-size: 62.5%;
}

.App {
  display: flex;
  align-content: space-between;
  align-items: stretch;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;

  height: 100vh;

  text-align: center;

  background-image: radial-gradient(
      rgb(233, 240, 243, 0.7),
      rgb(208, 208, 216, 0.7)
    ),
    url(./img/background.jpg);
  background-position: top;
  background-size: cover;
}

.App-header {
  display: flex;
  align-items: center;
  flex: 1 1 5vh;
  justify-content: center;

  min-height: 5vh;

  color: rgb(110, 109, 128);
  background-color: #282c34;

  font-size: 1.5rem;
}

.App-footer {
  display: flex;
  align-items: center;
  flex: 1 1 5vh;
  justify-content: center;

  min-height: 5vh;

  color: rgb(110, 109, 128);
  background-color: #282c34;

  font-size: 1.5rem;
}

.card {
  position: absolute;

  align-self: center;
  flex: 1 1 auto;

  width: 70vw;
  height: auto;
  padding: 1.5rem 0rem 1.5rem 0rem;

  border: 0.2rem solid white;
  border-radius: 01rem;
  background-color: rgba(239, 243, 245, 0.8);
  box-shadow: rgb(128, 128, 128, 0.3) 0rem 1rem 2.5rem;
}

.card__title {
  padding-bottom: 1.5rem;

  letter-spacing: 0.2rem;
  text-transform: uppercase;

  color: #bebebe;
  border-bottom: solid 0.2rem white;

  font-size: 3.2rem;
  font-weight: 300;
  line-height: 1.2;
}

.card__question {
  padding: 4rem 2rem;

  letter-spacing: 0.15rem;

  font-size: 2.5rem;
  font-weight: 600;
}

.card__question-primary {
  color: #6d6d6d;
}
.card__question-danger {
  color: #992e2e;
}
.card__question-congrats {
  padding-top: 4rem;

  color: #306f94;

  font-size: 3rem;
}

.form__group {
  position: relative;

  display: flex;
  align-content: center;
  align-items: stretch;
  flex-direction: column;
  justify-content: center;

  margin: 4rem 3vw 1rem 3vw;

  text-align: center;
}

.form__button,
.form__button:link,
.form__button:visited {
  align-self: center;
  flex: 0 1 auto;

  width: 60%;
  margin: 3.5rem 0 0 0;
  padding: 2rem;

  cursor: pointer;
  text-align: center;
  letter-spacing: 0.8rem;
  text-transform: uppercase;

  color: rgb(78, 82, 94);
  border: solid 0.25rem rgb(155, 158, 167);
  border-radius: 0.5rem;
  outline: none;
  background-color: transparent;

  font-size: 2rem;
  font-weight: 600;
}

.form__button:hover {
  color: white;
  border: solid 0.25rem #5a5f68;
  background-color: #5a5f68;
}

.form__button:active {
  transform: translateY(2px);

  outline: none;
}

.form__button:-moz-user-disabled {
  color: rgb(155, 158, 167);
  border: solid 2px rgb(155, 158, 167);
}

.form__button:disabled,
button[disabled] {
  cursor: not allowed;
  pointer-events: none;

  color: transparent;
  border: solid 2px transparent;
}

.full-bg {
  position: relative;

  display: flex;
  align-items: center;
  flex: 1 1 100%;
  flex-direction: column;
  justify-content: center;

  padding-bottom: 10vh;
}

#mainInput {
  padding-bottom: 0.6rem;

  transition: border-color 0.2s;
  text-align: center;
  letter-spacing: 0.1rem;

  color: rgb(54, 58, 70);
  border: none;
  border-bottom: 2px solid #c4c4c4;
  outline: 0;
  background: transparent;

  font-family: inherit;
  font-size: 3rem;
}
#mainInput:disabled {
  border-bottom: 2px solid transparent;
}

#mainInput:focus {
  padding-bottom: 0.6rem;

  border-width: 0.3rem;
}

#mainInput:focus ~ .form__label {
  position: absolute;
  top: 0;

  display: block;

  transition: 0.2s;

  font-size: 1rem;
}

#mainInput::placeholder {
  text-align: center;

  color: rgb(134, 138, 148);
}

#mainInput::-webkit-input-placeholder {
  text-align: center;

  color: rgb(134, 138, 148);
}

#mainInput::-moz-placeholder {
  text-align: center;

  color: rgb(134, 138, 148);
}

#mainInput:-moz-placeholder {
  text-align: center;

  color: rgb(134, 138, 148);
}

#mainInput:disabled::placeholder {
  text-align: center;

  color: rgb(134, 138, 148);
}

#mainInput:disabled::-webkit-input-placeholder {
  color: transparent;
}

#mainInput:disabled::-moz-placeholder {
  color: transparent;
}

#mainInput:disabled:-moz-placeholder {
  color: transparent;
}

#mainInput:placeholder-shown ~ .form__label {
  color: transparent;
}

#mainInput:required,
#mainInput:invalid {
  box-shadow: none;
}

.noselect {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  -webkit-touch-callout: none;
}
